<script setup>
import BaseHeader from '@/components/common/header/baseHeader.vue'
import BaseFooter from '@/components/common/footer/baseFooter.vue'
import PagingDiv from '../../components/common/paging/pagingDiv.vue'

import { useStore } from "vuex";
//composition API script setup tihs  .     router  
import { useRouter } from 'vue-router'
import axios from "axios";
import { ref, onBeforeMount} from 'vue';

import { AgGridVue } from 'ag-grid-vue3'; // Vue3 AgGrid Component

import 'ag-grid-community/styles/ag-grid.css' //   css
import 'ag-grid-community/styles/ag-theme-alpine.css' //  

import { changeXSS } from '../../utils/XSSutil'

  onBeforeMount(() => {
    getBoardList(1);
  });

  const store = useStore();
  const router = useRouter();

  const rowData = ref([]);
  const resultTotalCnt = ref(0);//.  
  const nowPage = ref(1);//.  

  const srcTitle = ref('');

  const getBoardList = async (param) => {
    store.commit("changeLoadingStatus", true);
    nowPage.value = param;
    await axios.post("/boardList.do",{nowPage:param,title:srcTitle.value})
    .then((res) => {
      console.log('',res.data.RESULT_LIST);
      if(res.data.RESULT_SIZE == 0) {
        rowData.value = [];
        resultTotalCnt.value = 0;
      } else {
        rowData.value = res.data.RESULT_LIST;

        res.data.RESULT_LIST.map(function(row) {
          row.BODY_TEXT = changeXSS(row.BODY_TEXT);
          row.TITLE = changeXSS(row.TITLE);
          return row;
        });
        resultTotalCnt.value = res.data.RESULT_TOTAL_CNT;
      }
    })
    .catch((res) => {
      console.error("", res);
      rowData.value = [];
//      return 'ERROR'
    });
    store.commit("changeLoadingStatus", false);
  }

  const colDefs = ref([
    { field: "CATEGORY",headerName: '', checkboxSelection: true, width: 120 },
    { field: "CODE_NAME", headerName: '', width: 110},
    { field: "TITLE" ,headerName: '',editable: true, width: 140 },
    { field: "BODY_TEXT", headerName: '', width: 150 },
    { field: "COMMENT_CNT", headerName: '', width: 80 },
    { field: "REG_DT2",headerName: '',editable: true, cellEditor: 'agDateStringCellEditor',
        cellEditorParams: {
            min: '2020-01-01',
            max: '2027-12-31',
        }, width: 120 }
  ]);

  let gridObject = {};
  const onMountGrid =(prop) =>{
    console.log(prop);
    gridObject = prop.api;
  }

  const onCellClicked = (e) => {
    console.log(e.colDef.field);
    if( e.colDef.field == 'CATEGORY' || e.colDef.field == 'CODE_NAME' 
//    || e.colDef.field == 'BODY_TEXT'
  ) {
      router.push('/boardDetail/'+e.data.SEQ);
    }
  }

  const saveBoardData =() =>{
/*    gridObject.startEditingCell({
      rowIndex: 0,
      colKey: 'TITLE'
    });
*/
    // eslint-disable-next-line no-debugger
//    debugger .         .    debugger;

    console.log(gridObject.getSelectedNodes());
  }

  const deleteBoardData=()=> {
    if(gridObject.getSelectedNodes().length==0) {
      alert('  ');
      return false;
    } else if (gridObject.getSelectedNodes().length > 1) {
      alert('   ');
      return false;
    }

    if(gridObject.getSelectedNodes()[0].data.COMMENT_CNT != null) {
      alert('   ');
      return false;
    }


    let seq = gridObject.getSelectedNodes()[0].data.SEQ;

    console.log(gridObject.getSelectedNodes());

    store.commit("changeLoadingStatus", true);
    axios.post("/deleteBoardOne.do",{SEQ:seq})
    .then((res) => {
      console.log('',res.data);
      if(res.data.RESCODE == '0000') {
        alert('.');
        getBoardList();
      } else {
        alert(res.data.RESMSG);
        return false;
      }
    })
    .catch((res) => {
      console.error("", res);
      alert(res);
    });
    store.commit("changeLoadingStatus", false);

  }

  const moveCreate = () => {
    router.push('/boardCreate');//history stack        . 
  }

  const pagingCallback = (param) => {
    console.log(param);
    getBoardList(param);
  }

</script>

<template>
  <BaseHeader title=""></BaseHeader>
  <div>
    <main>
    <div>
      <span>  </span><span style="float:right"> {{resultTotalCnt}}</span>
    </div>
    <div class="divRightAlign">
      <input v-model="srcTitle" placeholder="" @keyup.enter="getBoardList(1)" />
      <button type="button" id="searchBtn" @click="getBoardList(1)"></button>
      <button type="button" id="searchBtn" @click="moveCreate"></button>
      <button type="button" id="searchBtn" @click="saveBoardData"></button><br>
      <button type="button" id="searchBtn" @click="deleteBoardData"></button><br>
    </div>
    <div>
      <ag-grid-vue 
        style="height: 480px" 
        class="ag-theme-alpine" 
        :rowData="rowData" 
        :columnDefs="colDefs" 
        :localeText="{noRowsToShow: `  `}" 
        rowSelection="multiple"
        singleClickEdit = true 
        @grid-ready="onMountGrid"
        @cellClicked="onCellClicked"
      >
      </ag-grid-vue>
      <PagingDiv :nowPage="nowPage" :totalCnt="resultTotalCnt" :pagePerCnt=10 :viewPagingCnt=5 @pagingCallback="pagingCallback"/>
    </div>
  </main>
</div>
<BaseFooter></BaseFooter>
</template>

